@keyframes glitch-animation-1 {
  $steps: 20;
  @for $i from 0 through $steps {
    #{percentage($i*(1/$steps))} {
      clip-path: inset(
        percentage(random(100) / 100) 0% percentage(random(100) / 100) 0%
      );
    }
  }
}

@keyframes glitch-animation-2 {
  $steps: 20;
  @for $i from 0 through $steps {
    #{percentage($i*(1/$steps))} {
      clip-path: inset(
        percentage(random(100) / 100) 0% percentage(random(100) / 100) 0%
      );
    }
  }
}

@keyframes glitch-animation-3-xy {
  $steps: 60;
  $maxPercent: 90;
  @for $i from 0 through $steps {
    #{percentage($i*(1/$steps))} {
      $random: random(100) / 100;
      $top: if($random > 0.5, random($maxPercent) / 100, random(10) / 100);
      $left: if($random > 0.5, random(10) / 100, random($maxPercent) / 100);
      $bottom: if($random > 0.5, 1 - $top - 0.15, random(10) / 100);
      $right: if($random < 0.5, 1 - $left - 0.15, random(10) / 100);

      clip-path: inset(
        percentage($top) percentage($right) percentage($bottom) percentage($left)
      );
    }
  }
}

@keyframes glitch-animation-4-xy {
  $steps: 40;
  $maxPercent: 90;
  @for $i from 0 through $steps {
    #{percentage($i*(1/$steps))} {
      $random: random(100) / 100;
      $top: if($random > 0.5, random($maxPercent) / 100, random(30) / 100);
      $left: if($random > 0.5, random(30) / 100, random($maxPercent) / 100);
      $bottom: if($random > 0.5, 1 - $top - 0.2, random(30) / 100);
      $right: if($random < 0.5, 1 - $left - 0.2, random(30) / 100);

      clip-path: inset(
        percentage($top) percentage($right) percentage($bottom) percentage($left)
      );
    }
  }
}

@keyframes hue-rotate-90 {
  0% {
    filter: hue-rotate(90deg);
  }
  25% {
    filter: hue-rotate(0deg);
  }
  50% {
    filter: hue-rotate(-90deg);
  }
  75% {
    filter: hue-rotate(0deg);
  }
  100% {
    filter: hue-rotate(90deg);
  }
}

@keyframes hue-rotate-360 {
  0% {
    filter: hue-rotate(0deg);
  }
  25% {
    filter: hue-rotate(90deg);
  }
  50% {
    filter: hue-rotate(180deg);
  }
  75% {
    filter: hue-rotate(270deg);
  }
  100% {
    filter: hue-rotate(360deg);
  }
}
