@use '../../../../animations';

.car-div {
  --aspect-ratio: 3.78;
  height: 100%;
  width: max-content;
  position: relative;
  transform: translateX(var(--translateX));
  &__canvas {
    display: block;
    width: auto;
    height: 90%;

    // box-shadow:
    // inset 0 0 50px #fff,      /* inner white */
    // inset 20px 0 80px #f0f,   /* inner left magenta short */
    // inset -20px 0 80px #0ff,  /* inner right cyan short */
    // inset 20px 0 300px #f0f,  /* inner left magenta broad */
    // inset -20px 0 300px #0ff, /* inner right cyan broad */
    // 0 0 50px #fff,            /* outer white */
    // -10px 0 80px #f0f,        /* outer left magenta */
    // 10px 0 80px #0ff;         /* outer right cyan */;
  }
  &__wheel {
    height: 47%;
    // aspect-ratio: 1;
    position: absolute;
    transition: all 1s;
    bottom: 0px;
    &_forward {
      left: 74.7%;
    }
    &_backward {
      left: 15.3%;
    }
  }
  &__explosion {
    position: absolute;
    left: 65%;
    bottom: 30%;
    width: 20%;
  }
  &_ignite::after {
    content: '';
    background-image: url('../../../../assets/animations/ignite.gif');
    width: 32px;
    height: 32px;
    position: absolute;
    left: 65.7%;
    bottom: 50%;
  }
  &_borken::after {
    content: '';
    background-image: url('../../../../assets/animations/fire.gif');
    width: 100px;
    height: 100px;
    position: absolute;
    left: 60.7%;
    bottom: 20%;
    @media screen and (max-width: 1600px) {
      bottom: 10%;
      left: 60.7%;
    }
    @media screen and (max-width: 1000px) {
      bottom: -10%;
      left: 60.7%;
    }
    @media screen and (max-width: 600px) {
      bottom: -100%;
      left: 30.7%;
    }
  }
  &_rollForward {
    animation-iteration-count: infinite;
    animation-duration: 0.3s;
    animation-timing-function: linear;
    animation-name: car-div-anim;
    animation-direction: alternate;
    &::before {
      transform-origin: 50% 50%;
      content: '';
      background-image: url('../../../../assets/animations/exhause.gif');
      width: 100px;
      height: 100px;
      background-size: contain;
      position: absolute;
      left: 0%;
      transform: translate(-50%, 50%);
      bottom: 20%;
      animation: hue-rotate-360 1s linear infinite;
      @media screen and (max-width: 800px) {
        bottom: 40%;
        transform: translate(-50%, 50%) scale(0.5);
      }
    }
  }
  &_rollForward &__wheel {
    animation-iteration-count: infinite;
    animation-duration: 1s;
    animation-timing-function: linear;
    animation-name: tire-anim;
  }
}

@keyframes car-div-anim {
  from {
    filter: drop-shadow(-20px 0px 20px rgb(160, 0, 210));
    bottom: 0px;
  }
  to {
    filter: drop-shadow(-60px 0px 20px rgb(160, 0, 210));
    bottom: 3px;
  }
}

@keyframes tire-anim {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
