@use '../../../../../../../animations';
@import '../../../../../../../_mixins.scss';

.body {
  max-width: 500px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.btn-block {
  width: 100%;
  display: grid;
  grid-template-columns: auto auto;
  justify-items: center;
  align-items: center;
}

.btn {
  width: 160px;
}

.glitch-parent:hover .glitch {
  // --no-glitch: 1s;
  // @include glitched-text;
  --no-glitch: 5s;
  @include glitched-item;
}
