@use '../../animations';
@import '../../mixins';

.header {
  width: 100%;
  height: 64px;
  background-color: transparent;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 20px;
  opacity: 0.5;
  position: fixed;
  right: 0px;
  z-index: 1;
  transition: all 0.5s;
  &:hover {
    opacity: 0.9;
  }
  &__logo {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 80px;
    cursor: default;
    &:hover {
      > span {
        @include glitched-text;
      }
    }
    span {
      display: block;
      font-size: medium;
      height: 20px;
      width: 100%;
      text-align: center;
      text-transform: uppercase;
      &:nth-child(1) {
        color: var(--color-text-primary, white);
      }
      &:nth-child(2) {
        color: var(--color-text-secondary, rgba(139, 0, 139, 0.85));
        background-color: var(--color-bg-secondary, white);
      }
    }
  }
  &-nav {
    height: 100%;
    width: fit-content;
    &__item {
      height: auto;
    }
    &__anchor {
      &:hover > span {
        display: block;
        @include glitched-text;
        &::before {
          text-shadow: -2px 0 cyan;
        }
      }
      display: flex;
      flex-direction: column;
      justify-content: center;
      gap: 5px;
      align-items: center;
      padding: 10px;
      background-color: var(--color-main-lighter-transperant);
      border-radius: 5px;
      text-transform: uppercase;
      &_active {
        span {
          color: var(--color-main-lighter);
        }
      }
    }
    &_not-active {
      opacity: 0.5;
    }
    &_not-active &__item:hover &__anchor {
      @include glitched-item;
    }
    &__svg {
      height: 20px;
      width: 20px;
    }
    &__span {
      color: white;
      text-decoration: none;
    }
    &__list {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 20px;
      height: 100%;
      &_disabled {
        z-index: -1;
      }
    }
  }
}
